import React, { Component } from 'react'

export default class List1 extends Component {
    render() {
        return (
            <div>
                <h3>
                    正在进行
                    {this.props.todo.length}
                </h3>
                <ul>
                    {this.props.todo.map(
                        (item,idx)=><li key={idx}>
                            <input type="checkbox" onClick={(e)=>{this.props.finish(idx,e)}}/>
                            {item}<button onClick={()=>this.props.delList1(idx)}>删除</button></li>
                    )}
                </ul>
            </div>
        )
    }
}