import React, { Component } from 'react'

export default class Search extends Component {
    enterPress = (event) => {
        if(event.key === 'Enter') {
            this.props.enterPress(event.target.value)
            event.target.value = ''
        }
    }
    render() {
        return(
            <div>
                <input type="text" onKeyPress={this.enterPress}  placeholder="添加ToDo" required="required" autoComplete="off"/>
            </div>
        )
    }
}