import React, { Component } from 'react'
export default class List2 extends Component {
    render() {
        return (
            <div>
                <h3>
                    已经完成
                    {this.props.over.length}
                </h3>
                <ul>
                    {this.props.over.map(
                        (item,idx)=><li key={idx}>
                            <input type="checkbox" defaultChecked onClick={(e)=>{this.props.cancleFinish(idx,e)}}/>
                            {item}<button onClick={()=>this.props.delList2(idx)}>删除</button></li>
                    )}
                </ul>
            </div>
        )
    }
}