import React, { Component } from 'react';
import Search from './Search';
import List1 from './List1';
import List2 from './List2';

export default class Todolist extends Component {
    constructor(){
        super();
        this.state={
            todo:[],
            over:[]
        }
    }
    //添加
    add = (item)=>{
        var todo = [...this.state.todo,item];
        this.setState({
            todo:[...this.state.todo,item]
        })
        localStorage.setItem("todo",todo);
    }
    //"正在进行"中的事项删除操作
    delList1 = (idx)=>{
        var todo = this.state.todo;
        todo.splice(idx,1);
        this.setState({
            todo:todo
        })
    }
    //"已经完成"中的事项删除操作
    delList2 = (idx)=>{
        var over = this.state.over;
        over.splice(idx,1);
        this.setState({
            over:over
        })
    }
    

    //事项状态的转换
    finish = (idx,e) =>{
        var todo = this.state.todo;
        var over = [...this.state.over,todo[idx]];
    
        todo.splice(idx,1);
        this.setState({
            todo:todo,
            over:over
        })
        e.target.checked = false;
    }
    
    cancleFinish = (idx,e) =>{
        var over = this.state.over;
        var todo = [...this.state.todo,over[idx]];

        over.splice(idx,1);
        this.setState({
            todo:todo,
            over:over
        })
        e.target.checked = true;
    }

    render() {
        return (
            <div>
                <h2>Todolist</h2>
                <Search enterPress={this.add}/>
                <List1 todo={this.state.todo} delList1={this.delList1} finish={this.finish}/>
                <List2 over={this.state.over} delList2={this.delList2} cancleFinish={this.cancleFinish}/>
            </div>
        )
    }
}